<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <!-- build button for edit office  -->
      <v-btn
        class="ma-2"
        outlined
        small
        fab
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-icon>mdi-pencil-outline</v-icon>
      </v-btn>
      <!-- end  build button for add office  -->
    </template>
    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <!-- title card dialog  -->
        <v-card-title>
          <span class="text-h5">Edit office</span>
        </v-card-title>
        <!--  -->
        <v-card-text>
          <v-container>
            <v-row>
              <!-- Name field card  -->
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Office Name Ar *"
                  required
                  v-model="office.name.ar"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Office Name En *"
                  required
                  v-model="office.name.en"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Office Name Tr *"
                  required
                  v-model="office.name.tr"
                ></v-text-field>
              </v-col>
              <!--  -->

              <!-- Description field card  -->
              <v-col cols="12">
                <v-textarea
                  label="Office Description Ar *"
                  v-model="office.description.ar"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Office Description En*"
                  v-model="office.description.en"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Office Description Tr*"
                  v-model="office.description.tr"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <!--  -->
              <!-- selection field from areas  -->
              <!-- <build-areas-field :office="office" /> -->
              <v-col cols="12">
                <v-autocomplete
                  :rules="areasRules"
                  v-model="officeAreas"
                  label="Implementation areas*"
                  multiple
                  :items="allAreas"
                  item-text="name"
                  item-value="id"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      class="primary"
                      v-bind="data.attrs"
                      :input-value="data.name"
                      close
                      @click="data.select"
                      @click:close="remove(data.item)"
                    >
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                </v-autocomplete>
              </v-col>
              <!--end build  selection field from areas  -->

              <!-- selection field from nation  -->
              <build-nation-field
                v-on:changNation="changNation"
                :nation="office.nation_id"
              />
              <!-- end  selection field from nation  -->

              <!-- build image field  -->
              <v-col cols="12">
                <v-file-input
                  v-model="file"
                  chips
                  accept="image/png, image/jpeg"
                  label="Image"
                  @change="onFileChange"
              /></v-col>
              <v-col cols="12">
                <v-img
                  contain
                  max-height="250"
                  aspect-ratio="1"
                  :lazy-src="image ? image : minUrlImage + office.image_url"
                  :src="image ? image : minUrlImage + office.image_url"
                >
                </v-img>
              </v-col>
            </v-row>
          </v-container>
          <small color="red">*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> Cancel </v-btn>
          <v-btn
            color="secondary"
            class="add-office-button"
            text
            @click="updateOffice"
          >
            <span v-if="!progressEditOffice">Update Office</span>
            <v-progress-circular
              v-if="progressEditOffice"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>
<script>
import { officeController } from "./office";
export default {
  props: ["officeData"],
  data() {
    return {
      // form
      valid: true,
      // validation form
      nameRules: [(v) => !!v || "Name is required"],
      descriptionRules: [(v) => !!v || "Description is required"],
      areasRules: [(v) => !!(v && v.length) || "Choose at least office"],

      // image
      file: null,
      image: null,
      office: this.officeData,
      minUrlImage: this.$store.state.imageUlr + "office/",
      //
      progressEditOffice: false,
      isUpdating: false,
      dialog: false,
      allAreas: [],
      officeAreas: [],
      //  selectedAreas contains the id of areas from send this ids to server ..
    };
  },

  watch: {
    isUpdating(val) {
      if (val) {
        setTimeout(() => (this.isUpdating = false), 3000);
      }
    },
    dialog(stat) {
      if (stat) {
        this.getAreas();
      }
    },
  },
  components: {
    // "build-areas-field": () => import("./ArasField.vue"),
    "build-nation-field": () => import("../nation.vue"),
  },
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  methods: {
    remove(item) {
      const index = this.officeAreas.indexOf(item.id);
      if (index >= 0) this.officeAreas.splice(index, 1);
    },
    getAreas: async function () {
      this.allAreas = this.$store.getters.areas;
      const result = this.allAreas.map((item) => {
        return {
          name: item.name[this.lang],
          id: item.id,
        };
      });

      this.allAreas = result;

      this.officeAreas = [];
      this.office.areas.forEach((element) => {
        this.officeAreas.push(element.id);
      });
    },

    changeChoseArea(areas) {
      // recev data from chile component ( areas component  )
      this.office.areas = areas;
    },
    changNation(nation) {
      console.log(nation);
      this.office.nation_id = nation;
    },
    updateOffice: async function () {
      if (this.$refs.form.validate()) {
        this.progressEditOffice = true;
        var state = await officeController.updateOffice(
          this.file,
          this.office,
          this.officeAreas
        );
        if (state) {
          this.dialog = false;
          this.$emit("getVal");
          // this.restData();
        } else {
          this.progressEditOffice = false;
        }
      }
    },
    onFileChange(e) {
      if (e != null) {
        const reader = new FileReader();
        reader.readAsDataURL(this.file);
        reader.onload = (e) => {
          this.image = e.target.result;
        };
      } else {
        this.file = null;
        this.image = null;
      }
    },

    restData() {
      this.image = null;
      this.file = null;
      this.progressEditOffice = false;
    },
  },
};
</script>
 















